<template>
  <div class="table-section mt-5">
    <h2>{{ title }}</h2>

    <table class="table">
      <thead>
        <tr class="table-dark">
          <th scope="col">Id</th>
          <th scope="col">Name</th>
          <th scope="col">Code</th>
          <th scope="col">Weight</th>
          <th scope="col">Price</th>
          <th scope="col">Color</th>
          <th scope="col">Is Deleted</th>
          <th scope="col">Options</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in productList" :key="item._id">
          <td>{{ item._id }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.code }}</td>
          <td>{{ item.weight }}</td>
          <td>{{ item.price }}</td>
          <td>{{ item.color }}</td>
          <td>{{ item.isDeleted }}</td>
          <td>
            <EditProduct :product="item" />
            <button
              type="button"
              class="btn btn-danger"
              data-bs-toggle="modal"
              data-bs-target="#deleteModal"
            >
              Delete
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- {{productList}} -->
    <DeleteModal />
  </div>
</template>


<script>
import DeleteModal from './DeleteModal.vue';
import { API } from '@/api/CRUD';
import EditProduct from './EditProduct.vue';

export default {
    name: "ProductTable",
    props: {
      title: String,
    },
    data() {
      return {
        productList: null
      };
    },
    components: { DeleteModal, EditProduct },
    created() {
      API.getProducts().then(resp => this.productList = resp)
    }
};
</script>

<style scoped lang="scss">
.edit-btn {
  width: 20px;
  height: 20px;
}
</style>
